header {
  display: flex;
  align-items: center;
  background-color: var(--pws-bg-100);
  margin: 7.5px;
  padding: 12.5px 15px;
  font-size: 1rem;
  gap: 0.5rem;

  svg-icon {
    width: 1.3rem;
    height: 1.3rem;
  }
}

.hidden {
  display: none !important;
}

as-split-area {
  scrollbar-width: thin;
}

.mat-mdc-tree-node {
  min-height: 0 !important;
}

ul {
  padding-left: 1rem;
  padding-right: 1rem;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  background-color: var(--pws-bg-200);

  li {
    list-style: none;
    overflow: hidden;
    width: 100%;
    background-color: var(--pws-bg-200);
    cursor: pointer;
    display: flex;

    &.active {
      background-color: var(--pws-bg-100);
    }

    span {
      align-self: center;
    }
  }
}

.help-content {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

iframe {
  flex-grow: 1;
  display: block;
  border: 0 none;
}

.light-theme-alert {
  background: #2559b3;
  color: #fff;
  padding: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;

  svg-icon {
    width: 1.5rem;
    height: 1.5rem;
  }
}
